header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  height: 4rem;
  background-color: green;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  font-weight: bold;
}

li {
  display: inline-block;
  margin: 0 18px;
  list-style: none;
}
li a {
  color: white;

  text-decoration: none;
}

.moi a {
  text-decoration: none;
  color: rgb(3, 36, 3);
  font-weight: bold;
  font-size: 22px;
}
a:hover,
a:active {
  color: rgb(192, 183, 183);
}

.cta a {
  color: white;
  background-color: #ff1b68;
  padding: 8px 16px;
  border-radius: 8px;
}
.cta a:hover,
.cta a:active {
  color: #ff1b68;
  background-color: white;
}

.bg h1 {
  margin: 0;
  padding: 10px 0;
  color: rgb(255, 255, 255);
  font-weight: bold;
  position: absolute;
  bottom: 0;
  font-size: 1.6rem;
}
.bg {
  height: 33vh;
  background: url("https://images.unsplash.com/photo-1534413298607-48ba59e8a06d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom 300px center;
  background-attachment: fixed;
  position: relative;
}
@media (min-width: 40rem) {
  .bg {
    height: 50vh;
    background-position: bottom 250px top;
  }
  .bg h1 {
    font-size: 3rem;
  }
}

#plans {
  margin: 10px 0;
  text-align: center;
}

#plans h2 {
  margin: 0;
  padding: 5px 0px;
  color: green;
  font-weight: bold;
}
.plan {
  width: 100%;
  background: #d5ffdc;
  text-align: center;
  padding: 16px;
  margin: 8px;
}
@media (min-width: 40rem) {
  .plan {
    display: inline-block;
    width: 30%;
    vertical-align: middle;
    min-width: 17rem;
    max-width: 25rem;
  }
  .highlited {
    box-shadow: 2px 2px 2px 2px rgb(0, 0, 0, 0.5);
  }
}
.highlited {
  background: #19b84c;
  color: white;
}
.annotation {
  background: white;
  color: #19b84c;
  padding: 8px;
  border-radius: 10px;
  box-shadow: 2px 2px 2px 2px rgb(0, 0, 0, 0.5);
}
.features {
  list-style: none;
  padding: 0;
  margin: 0;
}
.features li {
  margin: 8px;
  display: block;
}
.btn {
  background: #0e4f1f;
  color: white;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #0e4f1f;
  cursor: pointer;
  font-weight: bold;
}
.btn:hover,
.btn:active {
  background: white;
  color: #0e4f1f;
}
.toggle {
  cursor: pointer;
  width: 5rem;
}

.sidebar {
  display: flex;

  background-color: white;
  width: 50%;
  height: 5px;
  border-radius: 10px;
  margin: 2px;
}
@media (min-width: 40rem) {
  .sidebar,
  .toggle {
    display: none;
  }
}
@media (max-width: 40rem) {
  .logo {
    display: none;
  }
  header {
    height: 4rem;
  }
  .items {
    display: none;
  }
}
.title {
  text-align: center;
  margin: 32px;
  padding: 5px 0px;
  color: white;
  font-weight: bold;
}
#key-features {
  background: #ff1b68;
  margin-top: 80px;
  padding: 16px;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
.list-item {
  vertical-align: top;
}
@media (min-width: 40rem) {
  .list-item {
    display: inline-block;
    width: 30%;
    min-width: 17rem;
    max-width: 25rem;
  }
}
.image {
  background-image: url("https://images.unsplash.com/photo-1644582698270-48c240dfa6f9?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 128px;
  /* border: 2px solid #424242; */
  border-radius: 50%;
  height: 128px;
  margin: auto;
}
.list-description {
  text-align: center;
  font-weight: bold;
  color: white;
  font-size: 20px;
}
footer {
  padding: 20px;
  background: black;
  color: white;
  text-align: center;
}
footer a {
  margin: 15px 25px;
  cursor: pointer;
}
